@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,500i,600,600i,700,700i,800,800i,900,900i|Press+Start+2P);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-size: 1.618rem;
    font-family: Montserrat;
}

.header {
    font-family: "Press Start 2P";
    color: white;
    font-weight: 500i;
    min-height: 200px;
    margin-bottom: 20px;
    background-image: url(/stock-images/static/media/header_bg.f4a4e6f2.jpg);
    background-position: bottom;
}

.header h1 {
    margin-top: 10%;
}

input, .number-button {
    width: 100%;
}

.all-imgs {
    margin-top: 20px;
    margin-bottom: 20px;  
}

.img-column {
    background-color: whitesmoke;
    border: 5px solid #fff;
}

.one-img {
        width: 100%;
        height: auto;
}

.center-block {
    display: block;
    margin-left: auto;
    margin-right: auto;
 }

.navigate-btns button {
    width: 100%;
    margin-bottom: 20px;
}

@media only screen and (min-width: 720px) {

    .one-img {
        width: 200px;
        height: 200px;
        object-fit: cover;
    }
    
    .img-column {
        height: 280px;
    }
    
}

